import { createRouter, createWebHashHistory, createWebHistory, RouteRecordRaw } from 'vue-router';
import { FrontRouter } from "./front/index";
import { BehindRouter } from './behind/index';
import { store } from '@/store';

const routes: Array<RouteRecordRaw> = [...FrontRouter, ...BehindRouter]
console.log(routes)

export const router = createRouter({
  history: createWebHistory(process.env.VUE_APP_ROUTER_BASE),
  // history: createWebHashHistory(process.env.VUE_APP_ROUTER_BASE),
  routes
})

router.beforeEach((to, from, next) => {
  if (!to?.meta?.noLogin && !store.state.token) {
    next('/login');
  } else {
    next();
  }
})

export default router
